.App {
  text-align: center;
  height:100vh; /*maybe delete this*/
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.logozz img {
  position:absolute;
bottom:8px;
right:8px;
max-width:32px;
max-height:32px;
min-width:32px;
max-height:32px;
cursor:pointer;

background:rgba(51, 51, 51, 0.5);
border-radius:50%;
}

.card {
  max-width:60vw;
  margin:0 auto;
  box-shadow: 4px 4px 10px 4px rgba(0,0,0,0.25);
  border-radius:0 10px 10px 0;
  background:#fff;
  color:#333;
  transition:all 250ms;
  display:grid;
  /* border-left: 4px solid #333; */
  z-index:5;
}

form input {
  border:none;
  margin:10vh 1vw;
  width: 25vw;
  height:6vh;
  border-radius:10px;
  background-color: white;
  box-shadow: 4px 4px 10px 4px rgba(0,0,0,0.25);
  z-index:5;
  transition:all 250ms;
  
}

form input:focus {
  outline: none;
}

form button {
  height:6vh;
  border-radius:10px;
  border:none;
  /* font-family:inherit; */
  font-weight:bold;
  background-color:white;
  cursor:pointer;
  color:#333;
  box-shadow: 4px 4px 10px 4px rgba(0,0,0,0.25);
  z-index:5;
}

.test {
  position:absolute;
  top:1vh;
  left:1vw;
  border-radius: 10px;
  cursor: pointer;
  transition: all 250ms;
  padding:1vh 2vw;
  font-size:24px;
  border:none;
  background-color:rgba(255, 255, 255, 0.25);
  font-family: inherit;
  color:white;
  box-shadow: 4px 4px 10px 4px rgba(0,0,0,0.25);
}

.test:hover {
  background-color:rgba(255, 255, 255, 0.75);
}

.rain {
  position:absolute;
  top:0;
  left:0;
  min-width:120vh;
  /* width:100vh; */
  /* height:100vh; */
  z-index:-1;
  animation: rainAnimation 5s;
  transform:scale(1.4)
}


@keyframes rainAnimation {
  from {
    opacity:0;
  }
  to {
    opacity:1;
    transform:scale(1.4)
  }
}

.cloud {
  position:absolute;
  top:10vh;
  left:-10vw;
  z-index:-1;
  animation: cloudAnimation 120s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

@keyframes cloudAnimation {
  from {
    opacity:0;
    transform:translateX(-10vw)
  }
  to {
    opacity:15;
    transform:translateX((100vw));
    left:110vw;
  }
}

.sun {
  position: absolute;
  top:2vh;
  left:2vw;
  animation: sunAnimation 5s;
}

@keyframes sunAnimation {
  from {
    top:-8vh;
    left:-8vw;
    opacity:0;
  }
  to {
    top:2vh;
    left:2vw;
    opacity:2;
  }
}

.info {
  display:block;
  margin:0 auto;
  padding:2vh 1vw;
  border:none;
  background:transparent;
  font-family:inherit;
  font-size:24px;
  font-weight:bold;
  cursor:pointer;
  color:#333;
}

.info:hover {
  color:rgb(29, 29, 29);
}

.popUp {
  background: rgba(255, 34, 34, 0.8);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  /* backdrop-filter: blur( 4px ); */
  /* -webkit-backdrop-filter: blur( 4px ); */
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );

  position:absolute;
  top:5vh;
  left:5vw;
  width:90vw;
  height:90vh;
  margin:auto;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  animation: popupAnim 250ms;

  transition: all 250ms;
  color:white;
}

@keyframes popupAnim {
  from {
    transform: scale(0);
  }
  to {
    transform:scale(1)
  }
}

@media (max-width: 800px) {
  .card {
    max-width:80vw;
  }
  form input {
    width:50vw;
    font-size:32px;
    font-weight:bold;
    color:black;
    height:8vh;
    opacity:0.8;
  }
  form button {
    height: 8vh;
    font-size:32px;
    opacity:0.8;
  }
  .sun {
    z-index:-2;
  }
}